
<template>
  <form action="/api/standards/standard" method="post" class="fon" @submit.prevent="submitForm" >
    <input name="tk" id="title" placeholder="tk" v-model="exspertise.tk">
    <input name="finalTime" placeholder="finalTime" v-model="exspertise.finalTime">
    <input name="type" placeholder="type" v-model="exspertise.type">
    <label for="standard">Выберите, пожалуйста стандарт</label>
    <select name="standard" class="form-control" id="standard" v-model="idStandard" @change="generateUrl()">
      <option v-for="item in standards" :key="item.id" :value="item.id" >{{item.id}}) {{ item.designation }}</option>
    </select>
    <p>{{idStandard}}</p>

    <input type="submit" class="btn" value="Добавить">
  </form>
</template>
<script>

import {AXIOS} from "../../http-common";

export default {
  name: 'FormForAddStandard',
  data(){
    return{
      exspertise:{
        tk:'',
        finalTime:'',
        type:''
      },
      standards:[],
      idStandard:null,
      url:''

    }
  },
  methods:{
    loadGreeting() {
      AXIOS.get('/standards/all')
          .then(response => {
            this.$data.standards = response.data;

          })
          .catch(error => {
            console.log('ERROR: ' + error.response.data);
          })

    },
      submitForm() {
        AXIOS.post(this.url, this.exspertise)
            .then(response => {
              console.log(response.data);
              // дальнейшая обработка ответа
            })
            .catch(error => {
              console.error(error);
              // обработка ошибок
            });
      },
    generateUrl(){
      this.url = '/exspertises/expertise/' + this.idStandard;


    }

    },
  mounted() {
      this.loadGreeting();
    }

}
</script>

<style scoped lang="scss">
.fon{
  background-color: aliceblue;
}
</style>